import React, {Component} from 'react';

export class Edit extends Component {

state = {
    id: this.props.data.id,
    title: this.props.data.title,
    desc: this.props.data.description,
    date: this.props.data.date,
}

onChangeTitle = (e) => {
    this.setState(
        {
            title: e.target.value,
        }
    )
}

onChangeDesc = (e) => {
    this.setState(
        {
            desc: e.target.value,
        }
    )
}
    onChangeDate = (e) => {
        this.setState(
            {
                date: e.target.value,
            }
        )
    }

onSubmit = (e) => {
    e.preventDefault();
    this.props.setEdited(this.state.id, this.state.title, this.state.desc, this.state.date);
    
}

render() {

    return (
        <div className="Add">
            <h2>Add announcement</h2>
            <form onSubmit={this.onSubmit}>
                <label>Title</label><input type='text' onChange={this.onChangeTitle}
                                           placeholder={this.props.data.title}/><br/>
                <label>Description</label><textarea  onChange={this.onChangeDesc} placeholder={this.props.data.description}/><br/>
                <label>Date</label><input type='date' onChange={this.onChangeDate} placeholder={this.props.data.date}/><br/>
                <button type='submit'>Edit</button>
            </form>
        </div>
    );
}
}