import React, {Component} from 'react';


export class Search extends Component {
    state ={
qwery: '',
    }
    searchChange = (e) => {
        const qwery = e.target.value;
        this.setState({qwery});
        this.props.onSearch(qwery);
    }
    render() {
        return (
            <div className="Search">

               <input onChange={this.searchChange}
                      placeholder='search'
                      className='search'
                      value={this.state.qwery}/>

            </div>
        );
    }
}
