import React, {Component} from 'react';
import './App.css';
import {Search} from './components/Search';
import Header from './components/Header';
import {Add} from './components/Add';
import {AnnounsmentList} from './components/AnnounsmentList';
import {Edit} from './components/Edit';

export default class App extends Component {

    state = {
        announcements: [{
            id: '0000',
            title: '1',
            description: 'lorem10',
            date: '1985-01-12'
        },
            {
                id: '0001',
                title: '11',
                description: 'lorem101111',
                date: '1985-11-11'
            }],

        qwery: '',
        tempEditing: {
            id: '',
            title: '',
            description: '',
            date: ''
        }
    }
    delItem = (id) => {
        this.setState(
            ({announcements}) => {

                return {
                    announcements: announcements.filter(a => a.id !== id),
                };

            }
        );
    }
    addItem = (title, description) => {
        let date = new Date();
        let data = date.getDate() + '.0' +
            date.getMonth() + '.' + date.getFullYear() + '  ' + date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds();
        const newAnnounsment = {
            id: Date.now(),
            title: title,
            description: description,
            date: data
        }
        this.setState(
            ({announcements}) => {

                return {
                    announcements: [...announcements, newAnnounsment],
                };

            }
        );
    }

    onEdit = (id, title, description, date) => {
        this.setState(
            ({tempEditing}) => {

                return {
                    tempEditing: {...tempEditing, id: id, title: title, description: description, date: date},
                };

            }
        );
        console.log(this.state.tempEditing);
    }
    setEdited = (id, title, description, date) => {

        this.setState(
            ({announcements}) => {
                const indx = announcements.findIndex(a => a.id === id);
                const oldData = announcements[indx];
                const newAnnounsment = {
                    ...oldData,
                    id: id,
                    title: title,
                    description: description,
                    date: date,
                };
                const newArr = [...announcements.slice(0, indx),
                    newAnnounsment,
                    ...announcements.slice(indx + 1)];
                return {
                    announcements: newArr,
                };

            }
        );
        console.log(this.state.announcements);
    }

    search = (announcements, qwery) => {
        if (qwery.length === 0) {
            return announcements;
        }
        return announcements.filter(a => {
                return a.title.indexOf(qwery) > -1
            }
        );
    }
    onSearch = (qwery) => {
        this.setState({qwery: qwery});
    }

    render() {
        const visibleA = this.search(this.state.announcements, this.state.qwery)
        return (
            <>
                <div className="Header">
                    <Header/>
                </div>
                <div className="Search">
                    <Search onSearch={this.onSearch}/>
                </div>
                <div className="App">
                    <Add AddAnnounc={this.addItem}/>
                    <AnnounsmentList
                        announcements={visibleA}
                        onDeleted={this.delItem}
                        onEdit={this.onEdit}/>
                    <Edit data={this.state.tempEditing} setEdited={this.setEdited}/>

                </div>
            </>
        );
    }
}
